.form {
  display: flex;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.411);
  width: 350px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  padding: 20px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.btn {
  width: min-content;
  margin-left: auto;
  margin-right: auto;
  outline: none;
  border: none;
  background: none;
  cursor: pointer;
  color: rgb(148, 148, 148);
}

.btn:hover,
.btn:focus {
  color: rgb(31, 159, 168);
  font-size: 17px;
}

.input {
  margin-bottom: 10px;
  outline: none;
  background: none;
  border: 0;
  border-bottom: 1px solid rgb(143, 142, 142);
}

label {
  font-size: 11px;
  margin-bottom: 8px;
  color: rgb(192, 189, 189);
}
